"use client";
import useStore from "@/context/store";
import { ProductCard } from "../card";
import { useQueries } from "@tanstack/react-query";
import { getProductSingle } from "@/api/product";

export const CompareProducs = () => {
  const { compares } = useStore(); // compares is an array of slugs
  console.log("Compare slugs:", compares);

  // Create a query for each product slug using useQueries
  const productQueries = useQueries(
    compares.map((slug) => ({
      queryKey: ["product", slug],
      queryFn: () => getProductSingle(slug),
      // Optionally, add configuration such as staleTime, retry, etc.
    }))
  );

  // Check if any query is still loading
  const isLoading = productQueries.some((query) => query.isLoading);
  if (isLoading) {
    return <div>Loading...</div>;
  }

  // Check if any query resulted in an error
  const isError = productQueries.some((query) => query.error);
  if (isError) {
    return <div>Error fetching products.</div>;
  }

  // Map through the queries to extract product data
  const products = productQueries.map((query) => query.data);

  return (
    <div>
      <div className="py-[15px] border-b border-superSilver px-6">
        <h3 className="relative text-base font-normal text-cerulean w-[140px] text-center before:absolute before:-bottom-[15px] before:left-0 before:w-full before:h-[5px] before:bg-gradient-to-r before:from-blue-400 before:to-cerulean">
          Все товары ({products.length})
        </h3>
      </div>
      <div className="pt-[19px] pb-[9px] px-6">
        <div className="grid grid-cols-4 gap-[21px]">
          {compares.map((item, index) => (
            <ProductCard key={index} product={item} />
          ))}
        </div>
      </div>
    </div>
  );
};

export default CompareProducs;
